.announcement-row {
  margin: 80px 0;
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.announcement-box {
  width: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 40px;
  border-top: 1px solid #ddd;
  border-bottom: 0.5px solid #ddd;

  font-weight: 200;
  font-style: normal;
  font-size: 16px;
  letter-spacing: .05em;
  line-height: 1.4em;
  text-transform: none;
  color: grey;
}

.announcement-box p {
  margin: 5px 0;
}

.announcement-box a {
  color: #dca72e;
  text-decoration: none;
  border-bottom: 1px solid rgba(220,167,46,.3);
  -webkit-transition: 0.1s linear;
  -o-transition: 0.1s linear;
  transition: 0.1s linear;
}

.announcement-box a:hover {
  color: rgba(220,167,46,.6);
  border-color: rgba(220,167,46,.3);
  -webkit-transition: 0.1s linear;
  -o-transition: 0.1s linear;
  transition: 0.1s linear;
}

#sxsw {
  margin-top: 30px;
  width: 250px;
  height: auto;
}



